import React, { useState } from 'react';
import './App.css';
import Welcome from './components/Welcome';
import Portfolio from './views/Portfolio';

function App() {
  const [buttonPhase, setButtonPhase] = useState(true);
  const defaultStyle = {
    transition: `opacity 200ms`,
    opacity: '1'
  };

  const transitionStyles = {
    entering: { opacity: '0' },
    entered: { opacity: '1' },
    exiting: { opacity: '0' },
    exited: { opacity: '0' }
  };

  const timeout = {
    appear: 500,
    enter: 500,
    exit: 500
  }

  return (
    <div className="App">
      <Welcome in={buttonPhase} phase={{ buttonPhase, setButtonPhase }}  defaultStyle={defaultStyle} transitionStyles={transitionStyles} timeout={timeout}/>
      {!buttonPhase ? <Portfolio in={!buttonPhase} phase={{ buttonPhase, setButtonPhase }} defaultStyle={defaultStyle} transitionStyles={transitionStyles} timeout={timeout}/> : null}
    </div>

  );
}

export default App;
