import React from 'react'
import { Transition } from 'react-transition-group';
const NextButton = (props) => {
    const style = {
        light: {
            backgroundColor: 'black',
            color: 'white',
            boxShadow: '0 0 10px grey',
        },
        dark: {
            backgroundColor: 'white',
            color: 'black',
            boxShadow: '0 0 10px grey',
        }
    }

    return (
        <Transition in={props.in} timeout={props.timeout} appear unmountOnExit>
            {state => (
                <button onClick={props.clicker} style={{ ...props.defaultStyle, ...props.transitionStyles[state], ...style[props.profile] }} className="next-btn">
                    <span className="material-icons">
                        keyboard_arrow_down
                    </span>
                </button>
            )}
        </Transition>
    )
}

export default NextButton
