import React from 'react'

const Jumbotron = (props) => {
    const style = {
        dark:{
            backgroundColor:'black',
            color:'white'
        },
        light:{
            backgroundColor:'white',
            color:'black'
        }
    }
    return (
        <div className="jumbotron" style={style[props.profile]}>
            {props.children}
        </div>
    )
}

export default Jumbotron
