import React, { useState, useEffect } from 'react'
import { Transition } from 'react-transition-group';
import NavBar from '../components/NavBar';
import Jumbotron from '../components/Jumbotron';
import Statement from '../components/Statement';
import NextButton from '../components/NextButton';
import Friends from './Friends';

const Portfolio = (props) => {
    const [view, setView] = useState(false);
    const [view2, setView2] = useState(false);
    const [view3, setView3] = useState(false);
    const [view4, setView4] = useState(false);
    const [view5, setView5] = useState(false);
    const [page, setPage] = useState(0);
    const [refresh, setRefresh] = useState(false);
    useEffect(() => {
        setTimeout(() => setView(true), 1500);
        setTimeout(() => setView2(true), 3000);
        setTimeout(() => setView3(true), 4500);
        setTimeout(() => setView4(true), 6000);
        setTimeout(() => setView5(true), 7500);
    }, [refresh])

    const nextPage = () => {
        setView(false);
        setView2(false);
        setView3(false);
        setView4(false);
        setView5(false);
        setPage(page + 1);
        setRefresh(!refresh);
    }
    const skipToPage = () => {
        setView(false);
        setView2(false);
        setView3(false);
        setView4(false);
        setView5(false);
        setPage(4);
        setRefresh(!refresh);
    }

    return (
        <Transition in={props.in} timeout={props.timeout} appear unmountOnExit>
            {state => (
                <div style={{ ...props.defaultStyle, ...props.transitionStyles[state] }} className="portfolio">
                    <NavBar skipper={skipToPage} phase={{ buttonPhase: props.phase.buttonPhase, setButtonPhase: props.phase.setButtonPhase }} />
                    {page === 0 ? <Jumbotron profile="dark">
                        <Statement statement={"My name is Pete."} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"I like to eat."} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view2} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"I like to sleep."} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view3} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"I make da code."} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view4} timeout={props.timeout} appear unmountOnExit />
                        <NextButton clicker={nextPage} profile="light" defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view5} timeout={props.timeout} appear unmountOnExit />
                    </Jumbotron> : null}
                    {page === 1 ? <Jumbotron profile="light">
                        <Statement statement={"Started self-taught."} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"Went to a bootcamp."} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view2} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"Became the bootcamp."} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view3} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"COVID destroyed the bootcamp."} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view4} timeout={props.timeout} appear unmountOnExit />
                        <NextButton clicker={nextPage} profile="dark" defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view5} timeout={props.timeout} appear unmountOnExit />
                    </Jumbotron> : null}
                    {page === 2 ? <Jumbotron profile="dark">
                        <Statement statement={"Knows Full-Stack"} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"Prefers Front-End"} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view2} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"Angular React"} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view3} timeout={props.timeout} appear unmountOnExit />
                        <Statement statement={"HTML CSS JS"} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view4} timeout={props.timeout} appear unmountOnExit />
                        <NextButton clicker={nextPage} profile="light" defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view5} timeout={props.timeout} appear unmountOnExit />
                    </Jumbotron> : null}
                    {page === 3 ? <Jumbotron profile="light">
                        <Statement statement={"Click on Friends to see amazing Engineers"} defaultStyle={props.defaultStyle} transitionStyles={props.transitionStyles} in={view} timeout={props.timeout} appear unmountOnExit />
                    </Jumbotron> : null}
                    {page === 4 ? <Jumbotron profile="dark">
                        <Friends />
                    </Jumbotron> : null}
                </div>
            )}
        </Transition>
    )
}

export default Portfolio
