import React, { useState } from 'react'
import { Transition } from 'react-transition-group';

const Welcome = (props) => {
    

    return (
        <Transition in={props.in} timeout={props.timeout} appear unmountOnExit>
            {state => (
                <div className="welcome-button">
                    <button style={{...props.defaultStyle,...props.transitionStyles[state]}} onClick={() => props.phase.setButtonPhase(!props.phase.buttonPhase)}>Welcome</button>
                </div>
            )}
        </Transition>
    )
}

export default Welcome