import React from 'react'
import { Transition } from 'react-transition-group';

const Statement = (props) => {
    return (
        <Transition in={props.in} timeout={props.timeout} appear unmountOnExit>
            {state => (
                <h1 style={{ ...props.defaultStyle, ...props.transitionStyles[state] }}>{props.statement}</h1>
            )}
        </Transition>
    )
}

export default Statement
