import React from 'react'

const Card = (props) => {
    const onClickHandler = (e) => {
        window.open(props.customLink);
    }
    return (
        <div className="card">
            <div className="card-content">
                <img src={props.url} alt="" />
                <button onClick={onClickHandler}>{props.name}</button>
            </div>
        </div>
    )
}

export default Card
